import React,{Component} from 'react'
import Boxlist from '../boxlist/Boxlist'
import Side from '../sidebar/Side'

 class Navbar extends Component{
    constructor(props){
        super(props)
    }
    render(){
        return(
            <div>
            <div id='bar'>
		    <p id='text'>SUAB FOOD CENTER</p></div>
            <div id='side'><Side/></div>
            <div id='main'><Boxlist/></div>
            <style jsx>{`
            #bar{
                background-color:rgba(230,0,0,0.2);
                padding-top: 1px;
                text-align: center;
                border: transparent;
                height: 150px;
                width: 100%;
                }
            #text{
                padding-top:10px;
                font-size: 3em;
                font-style: bold;
                color:rgba(0,230,0,0.9)
            }
            #main{
                margin-top:10em;
                display:inline-block;
                padding-left:10em;
            }
            #side{
                margin-top:10em;
                position:fixed;
            }
            #bar{
                position:fixed;
                overflow:scroll;
            }
        `}
            </style>
            </div>
        )
    }
}
export default Navbar



